button {
    position: relative;
    top: 21px;
    left: 4px;
    width: auto;
    min-height: 50px;
    padding: 0px 16px;
    border-width: 0px;
    border-radius: 25px;
    font-family: 'Prompt', sans-serif;
    font-size: 1.8rem;
    font-weight: 500;
    color: rgb(37, 88, 65);
    background-color: rgb(125, 206, 170);
    box-shadow: -4px 4px 0px 1px rgb(89, 145, 120);
    cursor: pointer;
    user-select: none;
}

button:hover {
    /* color: rgb(241, 239, 231); */
    background-color: rgb(115, 187, 154);
    transition: 100ms ease-in;
}

.pressed {
    box-shadow: 0px 0px 0px 0px rgba(37, 88, 65);
    transform: translate(-4px, 4px);
    transition: 50ms ease;
}